import {
    REGISTER_SUCCESS,
    REGISTER_FAIL,
    USER_LOADED,
    AUTH_ERROR,
    LOGIN_SUCCESS,
    LOGIN_FAIL, 
    LOGOUT
} from "../actions/types";

const initialState = {
    token: document.cookie["x-access-token"],
    isAuthenticated: null,
    loading: true,
    user: null
}
const deleteCookie = (name) => {
    document.cookie = name +'=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;';
  }
export default function(state=initialState, action) {
    const {type, payload} = action;
    switch (type) {
        case USER_LOADED:
            return{...state,
            isAuthenticated: true,
            loading: false,
            user: payload}
        case REGISTER_SUCCESS:
        case LOGIN_SUCCESS:
            return {
                ...state, 
                ...payload,
                isAuthenticated: true,
                loading: false
            }  
        case REGISTER_FAIL:
        case AUTH_ERROR: 
        case LOGIN_FAIL:
        case LOGOUT:
            deleteCookie("x-access-token");
            return {
                ...state,
                token: null,
                isAuthenticated: false,
                loading: false
            }
        default: return state
    }
}